<template>
  <!-- 搜索菜单部分的过渡效果 -->
  <transition name="fade">
    <div class="table-com-search" v-show="showSearch">
      <el-card class="search-card" shadow="never">
        <el-form :model="searchForm" label-width="100px" class="search-form">
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="平台订单号">
                <el-input 
                  v-model="searchForm.platform_order_no" 
                  placeholder="请输入平台订单号"
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="商户订单号">
                <el-input 
                  v-model="searchForm.merchant_order_no" 
                  placeholder="请输入商户订单号" 
                  clearable 
                  prefix-icon="Search"
                />
              </el-form-item>
            </el-col>
            <el-col :span="6" v-if="!isMerchant && !isAgent">
              <el-form-item label="商户">
                <el-select 
                  v-model="searchForm.merchant_id" 
                  placeholder="请选择商户" 
                  clearable
                  filterable
                  style="width: 100%"
                >
                  <el-option 
                    v-for="merchant in merchantList" 
                    :key="merchant.id" 
                    :label="merchant.merchant_name" 
                    :value="merchant.id" 
                  />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="支付状态">
                <el-select 
                  v-model="searchForm.pay_status" 
                  placeholder="请选择订单状态" 
                  clearable
                  style="width: 100%"
                >
                  <el-option label="全部" :value="''" />
                  <el-option label="已创建" :value="0" />
                  <el-option label="已打开" :value="4" />
                  <el-option label="已支付" :value="1" />
                  <el-option label="已关闭" :value="2" />
                  <el-option label="已退款" :value="3" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="6">
              <el-form-item label="分账状态">
                <el-select 
                  v-model="searchForm.royalty_status" 
                  placeholder="请选择分账状态" 
                  clearable
                  style="width: 100%"
                >
                  <el-option label="全部" :value="''" />
                  <el-option label="待分账" :value="0" />
                  <el-option label="分账中" :value="1" />
                  <el-option label="分账成功" :value="2" />
                  <el-option label="分账失败" :value="3" />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="分账类型">
                <el-select 
                  v-model="searchForm.royalty_type" 
                  placeholder="请选择分账类型" 
                  clearable
                  style="width: 100%"
                >
                  <el-option label="全部" :value="''" />
                  <el-option label="不分账" value="none" />
                  <el-option label="单笔分账" value="single" />
                  <el-option label="商家分账" value="merchant" />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="分账时间">
                <el-date-picker
                  v-model="dateRange"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  format="YYYY-MM-DD HH:mm:ss"
                  value-format="YYYY-MM-DD HH:mm:ss"
                  style="width: 100%"
                  @change="handleDateRangeChange"
                  clearable
                />
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item>
                <div class="search-buttons">
                  <el-button type="primary" @click="handleSearch">
                    <el-icon><Search /></el-icon>搜索
                  </el-button>
                  <el-button @click="handleReset">
                    <el-icon><Refresh /></el-icon>重置
                  </el-button>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-card>
    </div>
  </transition>

  <!-- 表头/按钮行 -->
  <div class="table-header ba-scroll-style">
    <el-button :icon="Refresh" @click="refreshData" color="#40485b"/>
    <!-- 订单统计条 - 精简版横向展示 -->
    <div class="table-statistics-bar">
      <span class="stat stat-amt">总金额: <b class="stat-num">¥{{ statistics.total_amount ? statistics.total_amount : 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat">总订单: <b class="stat-num">{{ statistics.total_count || 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat stat-amt">分账总额: <b class="stat-num">¥{{ statistics.royalty_total_amount ? statistics.royalty_total_amount : 0 }}</b></span>
      <span class="split">|</span>
      <span class="stat stat-succ">成功订单: <b class="stat-num">{{ statistics.success_count || 0 }}</b></span>
    </div>
    <!-- 搜索按钮 -->
    <div class="table-search">
      <el-button-group class="ml-4">
        <el-button :icon="Search" @click="toggleSearch" />
      </el-button-group>
    </div>
  </div>

  <!-- 表格内容 -->
  <div style="width: 100%; overflow-x: auto;" class="table-content">
    <el-table
        :data="data.data"
        border
        stripe
        max-height="600"
        style="width: 100%;"
        @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="40"/>
      <!-- 表格列定义：只显示要求的列 -->
      
      <!-- 分账主体列 -->
      <el-table-column label="分账主体" align="center" min-width="150">
        <template #default="{ row }">
          {{ row.royalty_subject_name || '-' }}
        </template>
      </el-table-column>
      
      <!-- 订单号列 -->
      <el-table-column prop="order_no" label="订单号" align="center" min-width="200">
        <template #default="{ row }">
          {{ row.order_no || row.platform_order_no || '-' }}
        </template>
      </el-table-column>
      
      <!-- 收款账号列 -->
      <el-table-column label="收款账号" align="center" min-width="180">
        <template #default="{ row }">
          {{ row.payee_account || '-' }}
        </template>
      </el-table-column>
      
      <!-- 订单金额列 -->
      <el-table-column label="订单金额" align="center" width="100">
        <template #default="{ row }">
          ¥{{ row.order_amount || '0.00' }}
        </template>
      </el-table-column>

      <!-- 分账金额列 -->
      <el-table-column label="分账金额" align="center" width="100">
        <template #default="{ row }">
          <span v-if="row.royalty_amount > 0">¥{{ row.royalty_amount }}</span>
          <span v-else>-</span>
        </template>
      </el-table-column>
      
      <!-- 分账方式列 -->
      <el-table-column label="分账方式" align="center" width="100">
        <template #default="{ row }">
          <el-tag v-if="row.royalty_method === 'manual'" type="warning">手动</el-tag>
          <el-tag v-else-if="row.royalty_method === 'auto'" type="success">自动</el-tag>
          <span v-else>-</span>
        </template>
      </el-table-column>
      
      <!-- 分账状态列 -->
      <el-table-column label="分账状态" align="center" min-width="120">
        <template #default="{ row }">
          <span v-if="!row.royalty_status_text || row.royalty_status_text === '-'">-</span>
          <el-tag v-else-if="row.royalty_status_text === '待分账'" type="info">待分账</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '分账中'" type="warning">分账中</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '分账成功'" type="success">分账成功</el-tag>
          <el-tag v-else-if="row.royalty_status_text === '分账失败'" type="danger">分账失败</el-tag>
          <span v-else>{{ row.royalty_status_text }}</span>
        </template>
      </el-table-column>
      
      <!-- 创建时间列 -->
      <el-table-column label="创建时间" align="center" min-width="160">
        <template #default="{ row }">
          {{ row.created_at || '-' }}
        </template>
      </el-table-column>
      
      <!-- 分账时间列 -->
      <el-table-column label="分账时间" align="center" min-width="160">
        <template #default="{ row }">
          {{ row.royalty_time || '-' }}
        </template>
      </el-table-column>
      
      <!-- 备注列 -->
      <el-table-column label="备注" align="center" min-width="150">
        <template #default="{ row }">
          <span v-if="row.royalty_status === 3 && row.royalty_error" style="color: #f56c6c;" :title="row.royalty_error">
            {{ row.order_remark || '-' }}
          </span>
          <span v-else>{{ row.order_remark || '-' }}</span>
        </template>
      </el-table-column>

      <!-- 操作列 -->
      <el-table-column label="操作" align="center" fixed="right" min-width="150">
        <template #default="scope">
          <el-button size="small" type="primary" @click="handleViewDetail(scope.row)">详情</el-button>
          <el-button 
            v-if="scope.row.can_manual_royalty" 
            size="small" 
            type="warning" 
            @click="handleManualRoyalty(scope.row)"
          >
            分账
          </el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

  <!-- 表格分页 -->
  <div class="table-footer">
    <div class="table-pagination">
      <el-pagination
          v-model:current-page="data.current_page"
          v-model:page-size="data.page_size"
          :page-sizes="[10, 50, 100, 200]"
          :background="true"
          prev-text="上一页"
          next-text="下一页"
          layout="total, sizes, prev, pager, next, jumper"
          :total="data.page_total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      />
    </div>
  </div>

  <!-- 详情对话框 -->
  <el-dialog v-model="detailVisible" title="分账订单详情" width="800px">
    <el-descriptions :column="2" border v-if="currentOrder">
      <el-descriptions-item label="平台订单号">{{ currentOrder.platform_order_no }}</el-descriptions-item>
      <el-descriptions-item label="商户订单号">{{ currentOrder.merchant_order_no }}</el-descriptions-item>
      
      <el-descriptions-item label="订单金额">¥{{ currentOrder.order_amount }}</el-descriptions-item>
      <el-descriptions-item label="分账金额">
        <span v-if="currentOrder.royalty_amount > 0">¥{{ currentOrder.royalty_amount }}</span>
        <span v-else>-</span>
      </el-descriptions-item>
      
      <el-descriptions-item v-if="isSystemAdmin" label="代理商">{{ currentOrder.agent?.agent_name || '-' }}</el-descriptions-item>
      <el-descriptions-item v-if="!isMerchant && !isAgent" label="商户">{{ currentOrder.merchant?.merchant_name || '-' }}</el-descriptions-item>
      <el-descriptions-item label="产品">{{ currentOrder.product?.product_name || '-' }}</el-descriptions-item>
      <el-descriptions-item v-if="!isMerchant && !isAgent" label="主体">{{ currentOrder.subject?.company_name || '-' }}</el-descriptions-item>
      
      <el-descriptions-item label="分账类型">
        <el-tag v-if="currentOrder.royalty_type === 'none'" type="info">不分账</el-tag>
        <el-tag v-else-if="currentOrder.royalty_type === 'single'" type="success">单笔分账</el-tag>
        <el-tag v-else-if="currentOrder.royalty_type === 'merchant'" type="warning">商家分账</el-tag>
        <span v-else>-</span>
      </el-descriptions-item>
      
      <el-descriptions-item label="支付状态">
        <el-tag v-if="currentOrder.pay_status === 0" type="info">已创建</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 4" type="warning">已打开</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 1" type="success">已支付</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 2" type="warning">已关闭</el-tag>
        <el-tag v-else-if="currentOrder.pay_status === 3" type="danger">已退款</el-tag>
      </el-descriptions-item>
      
      <el-descriptions-item label="分账状态">
        <span v-if="!currentOrder.royalty_status_text || currentOrder.royalty_status_text === '-'">-</span>
        <el-tag v-else-if="currentOrder.royalty_status_text === '待分账'" type="info">待分账</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '分账中'" type="warning">分账中</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '分账成功'" type="success">分账成功</el-tag>
        <el-tag v-else-if="currentOrder.royalty_status_text === '分账失败'" type="danger">分账失败</el-tag>
        <span v-else>{{ currentOrder.royalty_status_text }}</span>
      </el-descriptions-item>
      
      <el-descriptions-item v-if="currentOrder.royalty_record" label="收款人姓名">{{ currentOrder.royalty_record?.payee_name || '-' }}</el-descriptions-item>
      <el-descriptions-item v-if="currentOrder.royalty_record" label="收款人账号">{{ currentOrder.royalty_record?.payee_account || currentOrder.royalty_record?.payee_user_id || '-' }}</el-descriptions-item>
      <el-descriptions-item v-if="currentOrder.royalty_record?.alipay_royalty_no" label="分账单号">{{ currentOrder.royalty_record.alipay_royalty_no }}</el-descriptions-item>
      <el-descriptions-item v-if="currentOrder.royalty_record?.royalty_time" label="分账时间">{{ currentOrder.royalty_record.royalty_time }}</el-descriptions-item>
      
      <el-descriptions-item label="创建时间">{{ currentOrder.created_at }}</el-descriptions-item>
      <el-descriptions-item label="支付时间">{{ currentOrder.pay_time || '-' }}</el-descriptions-item>
      
      <el-descriptions-item label="备注" :span="2">
        <span v-if="currentOrder.royalty_status === 3 && currentOrder.royalty_error" style="color: #f56c6c;">
          {{ currentOrder.order_remark || '-' }}
        </span>
        <span v-else>{{ currentOrder.order_remark || '-' }}</span>
      </el-descriptions-item>
    </el-descriptions>
  </el-dialog>
</template>

<script lang="ts" setup>
import { Refresh, Search } from '@element-plus/icons-vue';
import { ref, onMounted, computed } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import request from '@/utils/request';

// 引入自定义插件和 API
import { useTableDataPlugin } from "@/utils/plugins/table.plugin.ts";

// 使用表格插件来管理表格的状态、数据等
const {
  loadData,
  data,
  refreshData,
  handleSizeChange,
  handleCurrentChange,
  searchParams,
  isVisible: showSearch,
  toggleVisibility: toggleSearch
} = useTableDataPlugin("/api/v1/admin/single-royalty/order-list");

// 获取今天的日期
const getTodayDate = () => {
  const today = new Date();
  const year = today.getFullYear();
  const month = String(today.getMonth() + 1).padStart(2, '0');
  const day = String(today.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

// 获取今天的开始和结束时间
const getTodayRange = () => {
  const todayDate = getTodayDate();
  return [
    `${todayDate} 00:00:00`,
    `${todayDate} 23:59:59`
  ];
};

// 日期范围 - 默认设置为当天 00:00:00 到 23:59:59
const dateRange = ref(getTodayRange());

// 搜索表单数据
const searchForm = ref({
  platform_order_no: '',
  merchant_order_no: '',
  merchant_id: '',
  pay_status: '',
  royalty_status: '',
  royalty_type: '',
  start_time: getTodayRange()[0],
  end_time: getTodayRange()[1]
});

// 处理日期范围变化
const handleDateRangeChange = (value: any) => {
  if (value && value.length === 2) {
    searchForm.value.start_time = value[0];
    searchForm.value.end_time = value[1];
  } else {
    searchForm.value.start_time = '';
    searchForm.value.end_time = '';
  }
};

// 商户列表
const merchantList = ref([]);

// 订单统计
const statistics = ref({
  total_count: 0,
  pending_count: 0,
  processing_count: 0,
  success_count: 0,
  failed_count: 0,
  total_amount: 0,
  success_amount: 0,
  royalty_total_amount: 0,
  success_rate: 0
});

// 详情对话框
const detailVisible = ref(false);
const currentOrder = ref(null);

// 获取用户信息判断是否是代理商
const userInfo = ref<any>({});
const isAgent = computed(() => {
  return userInfo.value?.is_agent === true;
});
// 判断是否是系统管理员（user_group_id === 1）
const isSystemAdmin = computed(() => {
  return userInfo.value?.user_group_id === 1;
});
// 判断是否是商户（user_group_id === 4）
const isMerchant = computed(() => {
  const groupId = userInfo.value?.user_group_id;
  return Number(groupId) === 4;
});

// 获取用户信息
const getUserInfo = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/user/info',
      method: 'GET'
    });
    if (res.status) {
      userInfo.value = res.data;
    }
  } catch (error) {
    console.error('获取用户信息失败:', error);
  }
};

// 获取商户列表
const getMerchantList = async () => {
  if (isMerchant.value || isAgent.value) return;
  
  try {
    const res = await request({
      url: '/api/v1/admin/order/merchant-list',
      method: 'GET'
    });
    if (res.status) {
      merchantList.value = res.data;
    }
  } catch (error) {
    console.error('获取商户列表失败:', error);
  }
};

// 获取订单统计
const getStatistics = async () => {
  try {
    const res = await request({
      url: '/api/v1/admin/single-royalty/order-statistics',
      method: 'GET',
      params: searchParams.value
    });
    if (res.status) {
      statistics.value = res.data;
    }
  } catch (error) {
    console.error('获取统计失败:', error);
  }
};

// 处理搜索
const handleSearch = async () => {
  // 将搜索表单数据同步到searchParams
  searchParams.value = { ...searchForm.value };
  await refreshData();
  await getStatistics();
};

// 处理重置
const handleReset = () => {
  const todayRange = getTodayRange();
  dateRange.value = todayRange;
  searchForm.value = {
    platform_order_no: '',
    merchant_order_no: '',
    merchant_id: '',
    pay_status: '',
    royalty_status: '',
    royalty_type: '',
    start_time: todayRange[0],
    end_time: todayRange[1]
  };
  searchParams.value = { ...searchForm.value };
  refreshData();
  getStatistics();
};

// 已选中的行
const selectedRows = ref<any[]>([]);
const handleSelectionChange = (rows: any[]) => {
  selectedRows.value = rows;
};

// 查看详情
const handleViewDetail = async (row: any) => {
  try {
    const res = await request({
      url: `/api/v1/admin/single-royalty/order-detail/${row.id}`,
      method: 'GET'
    });
    if (res.status) {
      currentOrder.value = res.data;
      detailVisible.value = true;
    }
  } catch (error) {
    console.error('获取详情失败:', error);
    ElMessage.error('获取详情失败');
  }
};

// 手动分账
const handleManualRoyalty = async (row: any) => {
  try {
    await ElMessageBox.confirm(
      `确定要对订单进行手动分账吗？\n订单号：${row.platform_order_no || row.order_no}`,
      '手动分账',
      {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      }
    );

    const res = await request({
      url: '/api/v1/admin/single-royalty/order-manual-royalty',
      method: 'POST',
      data: {
        order_id: row.id
      }
    });

    if (res.status) {
      ElMessage.success(res.msg || '分账成功');
      await refreshData();
    } else {
      ElMessage.error(res.msg || '分账失败');
    }
  } catch (error: any) {
    if (error !== 'cancel') {
      console.error('分账失败:', error);
      ElMessage.error(error.message || '分账失败');
    }
  }
};

// 在组件挂载时加载数据
onMounted(async () => {
  await getUserInfo();
  await getMerchantList();
  
  // 设置初始搜索参数为当天
  searchParams.value = { ...searchForm.value };
  
  loadData();
  await getStatistics();
});
</script>

<style scoped>
.stat { color: #606266; }
.stat-amt { color: #f6ad55; }
.stat-succ { color: #67c23a; }
.stat-rate { color: #409EFF; }
.table-statistics-bar {
  display: flex;
  align-items: center;
  gap: 18px;
  margin-left: 16px;
  font-size: 14px;
  line-height: 1.7;
}
.stat-num {
  font-weight: bold;
  margin-left: 3px;
}
.split {
  color: #ccc;
  margin: 0 8px;
}
</style>

